import React from 'react';
import { Box, Typography, IconButton, Paper, List } from '@mui/material';
import { PlayArrow, Delete, Shuffle, Add, Edit } from '@mui/icons-material';

function Collections({ collections, currentCollection, onSelectCollection, onAddCollection, onDeleteCollection, onShuffle, darkMode }) {
  return (
    <Paper elevation={3} sx={{ 
      p: 2,
      borderRadius: '12px',
      background: darkMode ? 'rgba(26,26,26,0.9)' : 'rgba(255,255,255,0.9)',
      mb: 2
    }}>
      <Box sx={{ display: 'flex', alignItems: 'center', mb: 2 }}>
        <Typography variant="h6" sx={{ flexGrow: 1 }}>My Collections</Typography>
        <IconButton onClick={onAddCollection}>
          <Add />
        </IconButton>
      </Box>
      
      {collections.map((collection) => (
        <Box key={collection.id} sx={{
          display: 'flex',
          alignItems: 'center',
          p: 1,
          bgcolor: currentCollection?.id === collection.id ? 'rgba(255,255,255,0.1)' : 'transparent',
          '&:hover': { bgcolor: 'rgba(255,255,255,0.1)' },
          borderRadius: 1,
          mb: 1
        }}>
          <Typography variant="body1" sx={{ flexGrow: 1 }}>
            {collection.name} ({collection.songs.length})
          </Typography>
          <IconButton size="small" onClick={() => onSelectCollection(collection)}>
            <PlayArrow />
          </IconButton>
          <IconButton size="small" onClick={() => onShuffle(collection)}>
            <Shuffle />
          </IconButton>
          <IconButton size="small" onClick={() => onDeleteCollection(collection.id)}>
            <Delete />
          </IconButton>
        </Box>
      ))}
    </Paper>
  );
}

export default Collections;