import React, { useState, useEffect } from 'react';
import { Box, Button, Slide, Typography } from '@mui/material';
import CloseIcon from '@mui/icons-material/Close';
import Logo from './Logo';  // Add this import

function AppBanner() {
  const [isAndroid, setIsAndroid] = useState(false);
  const [showBanner, setShowBanner] = useState(true);

  useEffect(() => {
    const userAgent = navigator.userAgent.toLowerCase();
    setIsAndroid(/android/.test(userAgent));
  }, []);

  if (!isAndroid || !showBanner) return null;

  return (
    <Slide direction="down" in={true}>
      <Box sx={{
        position: 'fixed',
        top: 0,
        left: 0,
        right: 0,
        zIndex: 1000,
        background: 'linear-gradient(45deg, #FF512F, #F09819)',
        padding: '12px 20px',
        display: 'flex',
        alignItems: 'center',
        justifyContent: 'space-between',
        boxShadow: '0 2px 20px rgba(0,0,0,0.2)'
      }}>
        <Box sx={{ display: 'flex', alignItems: 'center', gap: 2, flexGrow: 1 }}>
          <Logo size={32} />  {/* Replace AndroidIcon with Logo */}
          <Typography sx={{ color: 'white', flexGrow: 1 }}>
            Get the ElSound Android app for better experience!
          </Typography>
          <Button
            variant="contained"
            sx={{
              bgcolor: 'white',
              color: '#FF512F',
              '&:hover': {
                bgcolor: 'rgba(255,255,255,0.9)'
              }
            }}
            onClick={() => window.location.href = 'https://github.com/Eldego07/elsound-app/releases/download/v1.0.0/app-release.apk'}
          >
            Download App
          </Button>
        </Box>
        <Button
          sx={{ color: 'white', minWidth: 'auto', ml: 2 }}
          onClick={() => setShowBanner(false)}
        >
          <CloseIcon />
        </Button>
      </Box>
    </Slide>
  );
}

export default AppBanner;