import React, { useState, useEffect, useContext } from 'react';
import axios from 'axios';
import SearchBar from './components/SearchBar';
import Player from './components/Player';
import { Container, Typography, CircularProgress, Snackbar, Alert, Paper, Box, useMediaQuery } from '@mui/material';
import { useLocation, useNavigate } from 'react-router-dom';
import { IconButton } from '@mui/material';
import { Brightness4, Brightness7 } from '@mui/icons-material';
import { ThemeContext } from './context/ThemeContext';
import { encrypt, decrypt } from './utils/crypto';
import AppBanner from './components/AppBanner';
import { PlayArrow, Add, Shuffle, Delete, QueueMusic } from '@mui/icons-material';
import Collections from './components/Collections';

function App() {
  const [videos, setVideos] = useState([]);
  const [currentVideo, setCurrentVideo] = useState(null);
  const [error, setError] = useState(null);
  const [loading, setLoading] = useState(false);
  const [playlist, setPlaylist] = useState([]);
  const [volume, setVolume] = useState(100);
  const [isShuffleMode, setIsShuffleMode] = useState(false);
  const { darkMode, setDarkMode } = useContext(ThemeContext);
  const navigate = useNavigate();
  const location = useLocation();
  const isMobile = useMediaQuery('(max-width:600px)');

  return (
    <div className="App">
      <AppBanner />
      <Container maxWidth="lg" sx={{ py: 4, position: 'relative' }}>
        <Paper elevation={3} sx={{ 
          p: 3, 
          mb: 4, 
          borderRadius: '16px', 
          maxWidth: '800px',
          margin: '0 auto',
          background: darkMode 
            ? 'linear-gradient(145deg, rgba(26,26,26,0.9), rgba(45,45,45,0.9))'
            : 'linear-gradient(145deg, rgba(240,240,240,0.9), rgba(255,255,255,0.9))',
          backdropFilter: 'blur(10px)',
          border: '1px solid',
          borderColor: darkMode ? 'rgba(255,255,255,0.1)' : 'rgba(0,0,0,0.1)',
        }}>
          <SearchBar />
          <Box sx={{ 
            display: 'flex', 
            flexDirection: isMobile ? 'column' : 'row', 
            gap: 3,
            minHeight: 'calc(100vh - 200px)'
          }}>
            <Player />
            <Collections />
          </Box>
        </Paper>
      </Container>

      <Snackbar 
        open={!!error} 
        autoHideDuration={6000} 
        onClose={() => setError(null)}
      >
        <Alert onClose={() => setError(null)} severity="error" sx={{ 
          width: '100%',
          color: '#fff',
          backgroundColor: 'rgba(211, 47, 47, 0.9)'
        }}>
          {error}
        </Alert>
      </Snackbar>
    </div>
  );
}

export default App;